<template>
  <div id="outer">
    <!-- 1. 排班选择区域：三个下拉框 + 添加按钮 -->
    <div class="schedule-form" 
      style="margin-bottom: 20px; display: flex; gap: 15px; align-items: center;"
    >
      <!-- 初始时间下拉框 -->
      <el-select 
        v-model="selectedStartTime" 
        placeholder="选择初始时间" 
        style="width: 180px"
        :disabled="!timeOptions.length"
      >
        <el-option 
          v-for="time in timeOptions" 
          :key="time" 
          :label="time" 
          :value="time"
          :disabled="isTimeDisabled(time)" 
        ></el-option>
      </el-select>

      <!-- 结束时间下拉框 -->
      <el-select 
        v-model="selectedEndTime" 
        placeholder="选择结束时间" 
        style="width: 180px"
        :disabled="!selectedStartTime"  
      >
        <el-option 
          v-for="time in getEndTimeOptions()" 
          :key="time" 
          :label="time" 
          :value="time"
        ></el-option>
      </el-select>

      <!-- 号种下拉框 -->
      <el-select 
        v-model="selectedTypeText" 
        placeholder="选择号种" 
        style="width: 120px"
        :disabled="!selectedStartTime || !selectedEndTime"  
      >
        <el-option 
          label="专家号" 
          value="专家号"
          :disabled="getDuration() !== 60"  
        ></el-option>
        <el-option 
          label="普通号" 
          value="普通号"
          :disabled="getDuration() !== 30"  
        ></el-option>
      </el-select>

      <!-- 添加按钮 -->
      <el-button 
        type="primary" 
        @click="handleAddSchedule"
        :disabled="!selectedStartTime || !selectedEndTime || !selectedTypeText"
      >
        添加
      </el-button>
    </div>

    <!-- 2. 排班表格 -->
    <el-table
      :data="sortedTableData"
      style="width: 100%; margin-bottom: 20px;"
      :row-class-name="tableRowClassName"
      :empty-text="tableData.length === 0 ? '暂无排班信息，请添加' : '加载中...'"
    >
      <el-table-column type="index" label="编号" width="150"></el-table-column>
      <el-table-column prop="time" label="时段" width="200"></el-table-column>
      <el-table-column prop="typeText" label="号种" width="200"></el-table-column>
      <el-table-column prop="fee" label="费用" width="200"></el-table-column>
      <el-table-column label="操作" width="200">
        <template slot-scope="scope">
          <el-button 
            type="danger" 
            size="mini" 
            @click="handleRemoveSchedule(scope.row)"
          >
            移除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 3. 确认排版模板按钮 -->
    <div style="text-align: center; margin-top: 20px;">
      <el-button 
        type="success" 
        size="medium" 
        @click="confirmAllSchedules"
        :disabled="tableData.length === 0"  
      >
       确认排版模板
      </el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name:'DocSchedule',
  data(){
    return {
      tableData: [
        // // 初始示例数据（可删除）
        // { 
        //   time: '9:00-10:00', 
        //   type: 1,  // 1表示专家号
        //   typeText: '专家号',
        //   fee: '30元', 
        //   startTime: '9:00', 
        //   endTime: '10:00' 
        // },
        // { 
        //   time: '11:00-11:30', 
        //   type: 0,  // 0表示普通号
        //   typeText: '普通号',
        //   fee: '15元', 
        //   startTime: '11:00', 
        //   endTime: '11:30' 
        // }
      ],
      // 下拉选择绑定值
      selectedStartTime: '',  // 选中的初始时间
      selectedEndTime: '',    // 选中的结束时间
      selectedTypeText: '',   // 选中的号种文字（用于显示）
      // 基础时间配置：工作时间（上午9-12，下午14-17）+ 30分钟间隔
      workTimeRanges: [
        { start: 9, end: 12 },   // 上午
        { start: 14, end: 17 }   // 下午
      ],
      // 医生ID（实际项目中可能从登录信息中获取）
      doctorId: Number(this.$user.userId)  // 假设从全局用户信息获取
    }
  },
  computed:{
    timeOptions(){
      const options = []
      this.workTimeRanges.forEach(range => {
        for(let hour = range.start ; hour < range.end ; hour++){
          options.push(`${hour}:00`)
          options.push(`${hour}:30`)
        }
        // 补充结束整点数据
        options.push(`${range.end}:00`)
      })
      return options;
    },
    // 计算属性：返回按开始时间排序后的表格数据
    sortedTableData() {
      // 复制原数组避免修改原始数据
      return [...this.tableData].sort((a, b) => {
        // 将时间转换为分钟数进行比较
        const timeA = this.timeToMinutes(a.startTime);
        const timeB = this.timeToMinutes(b.startTime);
        return timeA - timeB; // 升序排列
      });
    }
  },
  methods:{
    // 辅助方法：将时间转换为分钟数（用于排序）
    timeToMinutes(time) {
      return time.split(':').reduce((total, part) => total * 60 + Number(part), 0);
    },
    // 结束时间选项
    getEndTimeOptions() {
      if (!this.selectedStartTime) return [];
      const options = [];
      const start = this.timeToMinutes(this.selectedStartTime);
      
      // 生成30/60分钟后的时间点
      const candidateTimes = [
        this.minutesToTime(start + 30),
        this.minutesToTime(start + 60)
      ];
    
      candidateTimes.forEach(time => {
        if (this.isValidEndTime(time) && this.isAvailableTimeSlot(time)) {
          options.push(time);
        }
      });
    
      return options;
    },
    // 新增辅助方法
    minutesToTime(totalMinutes) {
      const hours = Math.floor(totalMinutes / 60);
      const minutes = totalMinutes % 60;
      return `${hours}:${minutes.toString().padStart(2, '0')}`;
    }, 
    isValidEndTime(time) {
      const [hour] = time.split(':').map(Number);
      return hour < this.workTimeRanges[1].end; // 只需判断不超过下午结束时间
    },  
    isAvailableTimeSlot(endTime) {
      const newEnd = this.timeToMinutes(endTime);
      return !this.tableData.some(item => {
        const itemStart = this.timeToMinutes(item.startTime);
        const itemEnd = this.timeToMinutes(item.endTime);
        return newEnd > itemStart && newEnd < itemEnd;
      });
    },
    // 计算当前时间差
    getDuration(){
      if(!this.selectedStartTime || !this.selectedEndTime) return 0

      const [startHour, startMinute] = this.selectedStartTime.split(':').map(Number)
      const [endHour, endMinute] = this.selectedEndTime.split(':').map(Number)

      // 转换为总分钟数计算差值
      const startTotal = startHour * 60 + startMinute;
      const endTotal = endHour * 60 + endMinute;
      return endTotal - startTotal;
    },
    // 检查时间是否已经被占用
    isTimeDisabled(time) {
      const target = this.timeToMinutes(time);
      return this.tableData.some(item => {
        const start = this.timeToMinutes(item.startTime);
        const end = this.timeToMinutes(item.endTime);
        return target >= start && target < end;
      });
    },  
    // 添加单条排班
    async handleAddSchedule() {
      // 校验时间冲突
      if (this.isTimeDisabled(this.selectedStartTime)) {
        this.$message.error('该时间已被占用，请选择其他时间');
        return;
      }

      // 处理号种和费用
      const isExpert = this.selectedTypeText === '专家号';
      const type = isExpert ? 1 : 0;
      const fee = isExpert ? 30 : 50;

      // 构造排班数据
      const newSchedule = {
        time: `${this.selectedStartTime}-${this.selectedEndTime}`,
        type: type,
        typeText: this.selectedTypeText,
        fee: fee,
        startTime: this.selectedStartTime,
        endTime: this.selectedEndTime
      };

      // 添加到本地表格（暂不提交到后端，等待最终确认）
      this.tableData.push(newSchedule);
      this.$message.success('已添加到排班列表');
      
      // 重置选择
      this.selectedStartTime = '';
      this.selectedEndTime = '';
      this.selectedTypeText = '';
    },
    // 移除单条排班
    handleRemoveSchedule(row) {
      // 使用唯一时间标识定位原始数据
      const index = this.tableData.findIndex(item => item.time === row.time);
      if (index !== -1) {
        this.tableData.splice(index, 1);
        this.$message.success('已从排班列表移除');
      }
    },
    // 10. 确认所有排版并提交到后端
    async confirmAllSchedules() {
      // 确认提示
      this.$confirm('确定要提交当前所有排班信息吗？提交后将保存为排版模板', '确认提交', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        try {
          // 构造提交给后端的数据格式
          const submitData = {
            doctorId: this.doctorId,
            schedules: this.tableData.map(item => ({  
              time: item.time,    // 直接使用预格式化的time字段
              type: item.type,
              fee: item.fee
            }))
          };
          console.log(submitData)

          // 发送请求
          const response = await axios.post('http://192.168.130.45:3333/doctor/schedule', submitData);

          if (response.data.code === 200) {
            alert('排版模板提交成功！');
          } else {
            alert('提交失败：')
            console.error('提交失败',response.data.msg)
          }
        } catch (error) {
          console.error('提交排版模板失败', error);
          this.$message.error('网络错误，提交失败请重试');
        }
      }).catch(() => {
        // 取消提交
        this.$message.info('已取消提交');
      });
    },
    // 表格行样式
    tableRowClassName({ row }) {
      return row.type === 1 ? 'expert-row' : 'normal-row';
    }
  }
}
</script>

<style scoped>
.expert-row {
  background-color: #f0f7ff; /* 浅蓝色 */
}
.normal-row {
  background-color: #f9f9f9; /* 浅灰色 */
}
/* 表单区域样式 */
.schedule-form {
  padding: 10px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 0 5px rgba(0,0,0,0.05);
}
</style>