<template>
  <div id="doc-nav">
    <!--原本    :default-active="latestRoutePath" -->
    <el-menu 
      mode="horizontal" 
      :default-active="currentRoute"
      router 
      class="el-menu"
    >
    <!-- 好像无用，因为点击本来就能切换 -->
      <el-menu-item class="el-menu-item" index="/doctor/doc-info">个人信息</el-menu-item>
      <el-menu-item class="el-menu-item" index="/doctor/doc-schedule">排班设置</el-menu-item>
      <el-menu-item class="el-menu-item" index="/doctor/regis-manage">挂号管理</el-menu-item>
      <el-menu-item class="el-menu-item" index="/doctor/regis">医生注册</el-menu-item>
    </el-menu>
  </div>
</template>

<script>
// import { mapState } from 'vuex';

export default {
    name:'DocNav',
    data(){
        return {
          currentRoute:'/doctor/doc-info'
        }
    },
    watch:{
      $route:{
        immediate: true,
        handler(to){
          this.currentRoute = to.path
        }
      }
    }
}
</script>

