<template>
  <div id="login">
    <div class="title">
      <h1 class="the-title">医院挂号系统</h1>
    </div>
    <div class="login-container">
      <!-- 切换区 -->
      <div class="tab-header">
        <!-- 登录 -->
        <div 
          class="tab-item"
          :class="{ active: isLoginActive }"
          @click="changeToLogin"
        >
          登录
        </div>
        <!-- 患者注册 -->
        <div
          class="tab-item"
          :class="{ active: !isLoginActive }"
          @click="changeToRegister"
        >
          患者注册
        </div>
      </div>

      <!-- 表单区 -->
      <div class="form-content">
        <!-- 登录表单 -->
        <form 
          class="login-form"
          v-if="isLoginActive"
        >
          <div class="form-radio">
            <span>您的身份是：</span>
            <label>
              <input 
                type="radio" 
                name="role"
                :value="1"
                v-model="loginForm.role"
              >医生
            </label>
            <label>
              <input 
                type="radio" 
                name="role"
                :value="0"
                v-model="loginForm.role"
              >患者
            </label>
          </div>
          <div class="form-group">
            <label>账号</label>
            <input type="text" placeholder="请输入账号" v-model="loginForm.account">
          </div>
          <div class="form-group">
            <label>密码</label>
            <input type="text" placeholder="请输入密码" v-model="loginForm.password">
          </div>
          <button type="button" 
            class="submit-btn"
            @click="handleLogin"
          >登录</button>
        </form>

        <!-- 注册表单 -->
        <form 
          class="register-form"
          v-else
        >
          <div class="form-group">

            <label>新账号</label>
            <input type="text" placeholder="请设置账号" v-model="registerForm.account">
          </div>
          <div class="form-group">
            <label>密码</label>
            <input type="text" placeholder="请设置密码" v-model="registerForm.password">
          </div>
          <div class="form-group">
            <label>确认密码</label>
            <input type="text" placeholder="请再次输入密码" v-model="registerForm.confirmPwd">
          </div>
          <button 
            class="submit-btn"
            @click="handleRegister"
          >注册</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
    name:'TheLogin',
    data(){
        return {
            isLoginActive:true,
            //登陆表单数据
            loginForm:{
              account:'',
              password:'',
              role:''
            },
            //注册表单数据
            registerForm:{
              account:'',
              password:'',
              confirmPwd:''
            },
        }
    },
    methods:{
      async toLogin(){
        try{
          const url = 'http://192.168.130.45:3333/login'
          // const url = 'http://192.168.130.240:3334/user/login'
          const data = {
            username: this.loginForm.account,
            password: this.loginForm.password,
            role: this.loginForm.role
          }
          const response = await axios.post(url,data,{timeout:600000})
          //请求成功
          if(response.data.code === 200){
            const userAuth = {
              // 平级
              userId: response.data.userId,
              role: this.loginForm.role // 关键：存储角色
            }
            console.log(this.loginForm.role)
            const Authorization = response.data.msg
            localStorage.setItem('token', Authorization)

            console.log(Authorization)
            console.log(localStorage.getItem('token'))

            console.log('成功')

            localStorage.setItem('userAuth', JSON.stringify(userAuth))
      
            // 简化跳转逻辑
            const targetPath = this.loginForm.role === 1 
              ? '/doctor' 
              : '/patient'
            
            // 方案1：直接跳转（推荐）
            this.$router.push(targetPath)

            // 方案二：实例未准备好无法跳转：
            // setTimeout(() => this.$router.push(targetPath), 100)
          }
        }catch(error){
          console.error('请求失败',error)
        }
      },
      async toRegister(){
        try{
          const url = 'http://192.168.130.45:3333/patient/register'
          const data = {
            username: this.registerForm.account,
            password: this.registerForm.password,
            role: 0
          }
          const response = await axios.post(url,data)
          //请求成功?码！！！！！！！核对
          if(response.data.code === 200){
            alert('注册成功，请登录')
            this.isLoginActive = true
            // return
          }
        }catch(error){
          console.error('请求失败',error)
        }
      },
      changeToLogin(){
        this.isLoginActive = true
      },
      changeToRegister(){
        this.isLoginActive = false
      },
      handleLogin(){
        if(!this.loginForm.account || !this.loginForm.password){
          alert('请填写完整！')
          return
        }else{
          this.toLogin()
        }
      },
      handleRegister(){
        if(!this.registerForm.account || !this.registerForm.password || !this.registerForm.confirmPwd){
          alert('请填写完整！')
          return
        }else if(this.registerForm.password !== this.registerForm.confirmPwd){
          alert('密码不一致！请重新填写！')
          return
        }else{
          this.toRegister()
        }
      }
    }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}
.title {
  width: 400px;
  margin: 70px auto;
  text-align: center;
}
.the-title {
  display: inline-block;
  font-size: 40px;
  letter-spacing: 5px;
}
.login-container {
  height: 400px;
  width: 350px;
  margin: 50px auto;
  border: 1px solid #eee;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0,0,0,0.05);
}

/* 顶部切换栏：横向排列 */
.tab-header {
  display: flex;
  margin-bottom: 25px;
  border-bottom: 1px solid #eee;
}

/* 切换项样式 */
.tab-item {
  flex: 1;
  text-align: center;
  padding: 10px 0;
  cursor: pointer;
  font-size: 16px;
  color: #666;
  position: relative;
  transition: color 0.3s;
}

/* 激活态样式：文字变色 + 底部下划线 */
.tab-item.active {
  color: #1677ff; /* 腾讯蓝，模拟 QQ 风格 */
}
.tab-item.active::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #1677ff;
}

/* 表单样式 */
.form-content {
  width: 80%;
  margin: 0 auto;
}
.form-group {
  margin-bottom: 20px;
}
.form-group label {
  display: block;
  margin-bottom: 8px;
  color: #333;
}
.form-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

/* 按钮样式 */
.submit-btn {
  width: 100%;
  padding: 12px;
  margin-top: 15px;
  background-color: #1677ff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}
.submit-btn:hover {
  background-color: #0f62d9;
}
.form-radio span {
  display: block;
  margin-bottom: 10px;
}
.form-radio label {
  margin-right: 10px;
}
.form-radio {
  margin-bottom: 10px;
}
</style>