<template>
  <div id="doc-regismanage">
    <el-table
      :data="tableData"
      style="width: 100%"
      
    >
      <el-table-column
        prop="name"
        label="患者姓名"
        width="220"
      >
      </el-table-column>
      <el-table-column
        prop="time"
        label="挂号时段"
        width="220"
      >
      </el-table-column>
      <el-table-column
        prop="type"
        label="号种"
        width="220"
      >
      </el-table-column>
      <el-table-column
        prop="lost"
        label="患者失约次数"
        width="220"
      >
      </el-table-column>
      <el-table-column
        label="当前状态"
      >
        <template slot-scope="scope">
          <el-dropdown 
            @command="(newVal) => handleStatusChange(newVal, scope.row)" 
            popper-append-to-body
          >
            <el-button :type="scope.row.status.type">
              {{ scope.row.status.text }}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                v-for="status in allStatuses"
                :key="status.value"
                :command="status.value"
                :disabled="status.value === scope.row.status.value"
              >
              {{ status.text }}
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios';

export default {
    name:'RegisManage',
    data(){
      return {
         tableData: [
            // {
            //   name:'王小虎',
            //   time:'9:00-9:30',
            //   type: 0, // 原始数字类型
            //   typeText: '普通号', // 用于显示转换后的文字
            //   lost: 3,
            //   status:{ value: 'pending', text: '待就诊', type: 'primary' },
            //   patientId: 'P1001', // 补充患者ID
            //   id: 'R2001' // 补充挂号记录ID
            // }, 
            // {
            //   name:'李小华',
            //   time:'9:00-10:00',
            //   type: 1, // 原始数字类型
            //   typeText: '专家号',
            //   lost: 2,
            //   status:{ value: 'pending', text: '待就诊', type: 'primary' },
            //   patientId: 'P1002',
            //   id: 'R2002'
            // }, 
            // {
            //   name:'张小明',
            //   time:'9:00-9:30',
            //   type: 0, // 原始数字类型
            //   typeText: '普通号',
            //   lost: 0,
            //   status:{ value: 'completed', text: '已完成', type: 'success' },
            //   patientId: 'P1003',
            //   id: 'R2003'
            // },
        ],
        allStatuses: [
          { value: 'pending', text: '待就诊', type: 'primary' },
          { value: 'completed', text: '已完成', type: 'success' },
          { value: 'lost', text: '失约', type: 'danger' },
        ],
        statusMap: {
          0: { value: 'pending', text: '待就诊', type: 'primary' },
          1: { value: 'completed', text: '已完成', type: 'success' },
          2: { value: 'lost', text: '失约', type: 'danger' }
         },
        typeMap: {
          0: '普通号',
          1: '专家号'
        },
      }
    },
    async created(){
      await this.fetchPatientInfo()

      // 每个患者
      for(let i = 0; i< this.tableData.length; i++){
        const patient = this.tableData[i]
        const times = await this.getLostTimes(patient.patientId)
        this.$set(this.tableData[i],'lost',times)
      }
    },
    methods:{
      async fetchPatientInfo(){
        try {
          const response = await axios.get('http://192.168.130.45:3333/doctor/regis-message',{
            params:{
              userId:this.$user.userId
            }
          })
          console.log(this.$user.userId)
          if(response.data.code === 200){
            const backEndDataList = response.data.data
            console.log(backEndDataList)
            this.tableData = backEndDataList.map(item => ({
              ...item,
              // 保留原始数字类型用于可能的后端交互
              // originalType: item.type,
              // 转换号种为文字显示
              // typeText: this.typeMap[item.type],
              // 转换状态为对象
              status: this.statusMap[item.currentStatus]
            }))
            console.log(this.tableData)
            console.log('获取医生看见的挂号信息成功！')
          }
        }catch(err){
          console.error('获取患者信息失败',err)
          this.$message.error('加载失败，请重试')
        }
      },
      async getLostTimes(patientId){
        try {
          const response = await axios.get('http://192.168.130.45:3333/doctor/selectMissNum',{
            params:{
              patientId: patientId
            }
          })
          if(response.data.code === 200){
            console.log('获取失约次数成功！')
            return response.data.data
          }
          // 默认0
          return 0;
          }catch(err){
            console.error(`获取患者${patientId}失约次数错误`, err);
            return 0; // 出错时默认返回0次
          }
      },
      async sendNewStatus(sendRequestList){
        try {
          const response = await axios.post('http://192.168.130.45:3333/doctor/updateStatus',sendRequestList)
          if(response.data.code === 200){
            alert('状态更改成功')
            console.log('修改患者状态成功！')
          }
        }catch(err){
          console.error('发送失败',err)
        }
      },
      handleStatusChange(newVal,row){
        const matchedStatus = this.allStatuses.find(
          (status) => status.value === newVal
        )
        if(matchedStatus){
          row.status = matchedStatus; // 更新当前行的状态

          // 发送修改后结果的逻辑!!!!!!!这里返回的数字
          const statusValue = Object.keys(this.statusMap).find(
            key => this.statusMap[key].value === newVal
          )

          //发送给后端的数据
        const sendRequestList = {
          userId: this.$user.userId,
          patientId: row.patientId,
          newStatus: Number(statusValue),
          // 每一条挂号信息的id,后端根据这个找？？？？
          id: row.id
        }
        console.log(sendRequestList)
        // 发请求
        this.sendNewStatus(sendRequestList)
        }
      }
    }
}
</script>

