<template>
  <div id="outer">
    <!-- 上方医生信息 -->
    <div class="doctor-card">
      <!-- 卡片标题栏 -->
      <div class="card-header">
        <h3 class="header-text">医生信息</h3>
        <div class="header-divider"></div>
      </div>

      <!-- 医生核心信息区 -->
      <div class="doctor-info-wrap">
        <div class="info-item">
          <span class="info-label">姓名：</span>
          <span class="info-content">{{ doctorInfo.name }}</span>
        </div>
        <div class="info-item">
          <span class="info-label">性别：</span>
          <span class="info-content">{{ doctorInfo.gender }}</span>
        </div>
        <div class="info-item">
          <span class="info-label">科室：</span>
          <span class="info-content">{{ doctorInfo.office }}</span>
        </div>
        <div class="info-item">
          <span class="info-label">职位：</span>
          <span class="info-content">{{ doctorInfo.position }}</span>
        </div>
        <div class="info-item info-expertise">
          <span class="info-label">擅长方向：</span>
          <span class="info-content">{{ doctorInfo.study }}</span>
        </div>
      </div>
    </div>

    <!-- 下方患者姓名 -->
    <div class="register-form-container">
    <!-- 表单标题 -->
      <h3 class="form-title">挂号信息填写</h3>
      
      <!-- Element UI 表单（去除校验相关属性） -->
      <el-form 
        :model="registerForm" 
        ref="registerFormRef" 
        label-width="80px" 
        class="form-content"
      >
        <!-- 患者姓名 -->
        <el-form-item label="患者姓名">
          <el-input 
            v-model="registerForm.patientName" 
            placeholder="请输入患者真实姓名" 
            maxlength="10"
          >
          </el-input>
        </el-form-item>  
        <!-- 确认按钮 -->
        <el-row class="btnRow">
          <el-button 
            type="primary" 
            class="btn" 
            size="small"
            @click="saveName"
          >确认</el-button>
        </el-row>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
    name:'ReCard',
    props:{
      doctorInfo:{
        type:Object,
        require:true,
        default:()=>({
          docterId: '未知',
          name: '未知',
          gender: '未知',
          office: '未知',
          position: '未知',
          study: '未知'
        })
      }
    },
    data(){
        return {
          registerForm:{
            patientName:''
          }
        }
    },
    methods:{
      saveName(){
        this.$bus.$emit('getName',this.registerForm.patientName)
      }
    }
}
</script>

<style scoped>
.doctor-card {
  width: 280px; /* 固定宽度，适配左右布局 */
  height: 250px;
  padding: 22px;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.06); /* 浅阴影提升层次感 */
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* 卡片标题栏 */
.card-header .header-text {
  font-size: 16px;
  color: #333333;
  margin: 0;
  font-weight: 500;
}
.card-header .header-divider {
  width: 45px;
  height: 2px;
  background: #409eff; /* 主色调分割线，呼应表单风格 */
  margin-top: 8px;
}

/* 信息区：保证标签对齐，内容易读 */
.doctor-info-wrap {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.info-item {
  display: flex;
  flex-wrap: wrap; /* 防止擅长内容过长溢出 */
  align-items: flex-start; /* 多行内容顶部对齐 */
  font-size: 15px;
}
/* 信息标签：固定宽度+灰色，统一对齐 */
.info-label {
  width: 80px;
  color: #666666;
  line-height: 1.5;
}
/* 信息内容：黑色+合适行高，提升可读性 */
.info-content {
  color: #333333;
  line-height: 1.6;
  flex: 1; /* 内容占剩余宽度，避免标签与内容挤在一起 */
}
/* 擅长方向单独优化：防止内容过长换行后与标签错位 */
.info-expertise {
  align-items: flex-start;
}

/* 姓名填写 */
.register-form-container {
  margin-top: 10px;
  padding: 20px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  /* min-width: 350px; */
  max-width: 500px;
}

.form-title {
  font-size: 18px;
  color: #333;
  margin: 0 0 18px 0;
  padding-bottom: 10px;
  border-bottom: 1px solid #f0f0f0;
}
.btn {
  width: 100px;
}
.btnRow {
  padding-left: 80px;
}
</style>