<template>
  <!-- 头部导航容器：固定在顶部，全屏宽度 -->
  <header class="app-header">
    <div class="header-container">
      <!-- 左侧：大标题 -->
      <div class="header-title">
        医院挂号系统
      </div>

      <!-- 右侧：退出登录按钮 -->
      <button 
        class="logout-btn" 
        @click="handleLogout"
      >
        退出登录
      </button>
    </div>
  </header>
</template>

<script>
export default {
  name: "MyHead",
  methods: {
    // 退出登录逻辑：清除本地存储+跳回登录页
    handleLogout() {
      // 1. 清除本地存储的用户信息（ID、Token等）
      localStorage.removeItem("userAuth");
      localStorage.removeItem('token')
      // 2. 清除Vue原型上的用户数据
      this.$user.userId = "";
      // 3. 跳回登录页
      this.$router.push("/login");
      // 4. 提示用户
      alert("已成功退出登录");
    }
  }
};
</script>

<style scoped>
.app-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  z-index: 999; /* 确保头部在最上层，不被其他内容覆盖 */
}

/* 头部内容容器：控制左右布局，限制宽度 */
.header-container {
  width: 100%;
  max-width: 1440px; 
  height: 100%;
  margin: 0 auto; 
  padding: 0 30px; 
  display: flex; 
  align-items: center;
  justify-content: space-between; 
  box-sizing: border-box;
}

/* 左侧标题样式 */
.header-title {
  font-size: 30px;
  font-weight: 700; /* 加粗 */
  color: #096dd9; /* 医疗蓝，和登录页风格统一 */
  letter-spacing: 1px; /* 字间距，更舒展 */
}

/* 右侧退出按钮样式 */
.logout-btn {
  padding: 10px 20px;
  background-color: #fff;
  border: 1px solid #096dd9;
  color: #096dd9;
  border-radius: 4px;
  cursor: pointer;
  font-size: 15px;
  transition: all 0.3s ease; /*  hover 过渡，更丝滑 */
}

/* 按钮 hover 效果：背景变蓝，文字变白 */
.logout-btn:hover {
  background-color: #096dd9;
  color: #fff;
}

</style>