<template>
  <div id="app-container">
    <!-- 医生信息卡片（居中显示） -->
    <div class="doctor-card">
      <!-- 卡片标题栏 -->
      <div class="card-header">
        <h3 class="header-text">医生信息</h3>
        <div class="header-divider"></div>
      </div>

      <!-- 医生核心信息区 -->
      <div class="doctor-info-wrap">
        <div class="info-item">
          <span class="info-label">姓名：</span>
          <span class="info-content">{{ doctorInfo.name }}</span>
        </div>
        <div class="info-item">
          <span class="info-label">年龄：</span>
          <span class="info-content">{{ doctorInfo.age }}</span>
        </div>
        <div class="info-item">
          <span class="info-label">科室：</span>
          <span class="info-content">{{ doctorInfo.office }}</span>
        </div>
        <div class="info-item">
          <span class="info-label">职位：</span>
          <span class="info-content">{{ doctorInfo.position }}</span>
        </div>
        <div class="info-item info-expertise">
          <span class="info-label">擅长方向：</span>
          <span class="info-content">{{ doctorInfo.study }}</span>
        </div>
      </div>

      <!-- 卡片底部装饰线 -->
      <div class="card-footer-line"></div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'DocInfo',
  data() {
    return {
      doctorInfo: {
        // name: '张三',
        // age: '45岁',
        // office: '心血管内科',
        // position: '主任医师',
        // study: '冠心病、心力衰竭、心律失常的诊断与治疗，尤其擅长心脏介入手术'
      }
    }
  },
  created(){
    this.getInfo()
  },
  methods:{
    async getInfo(){
      try {
        const response = await axios.get('http://192.168.130.45:3333/doctor/show',{
          params:{
            userId: this.$user.userId
          }
        })

        // const token = localStorage.getItem('token')
        // const response = await axios.get('http://192.168.130.240:3334/patient/register/doctor',{
        //   params:{
        //     userId: this.$user.userId,
        //   },
        //   headers:{
        //     Authorization: token
        //   }
        // })

        if(response.data.code === 200){
          this.doctorInfo = response.data.data
          console.log(this.doctorInfo)
          console.log('医生信息请求成功')
        }
      }catch(err){
        console.error('请求失败',err)
      }
    }
  }
}
</script>

<style scoped>
/* 页面容器：居中布局 */
#app-container {
  background-color: #ffffff; /* 纯白背景 */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

/* 医生卡片：缩小尺寸，优化间距 */
.doctor-card {
  width: 300px; /* 缩小宽度 */
  min-height: 280px; /* 适度调整高度 */
  padding: 24px; /* 调整内边距 */
  background: #ffffff;
  border-radius: 14px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.07);
  display: flex;
  flex-direction: column;
  gap: 22px; /* 调整内部间距 */
  position: relative;
  border: 1px solid #f0f0f0; /* 增加细边框提升质感 */
}

/* 卡片标题栏 */
.card-header {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.header-text {
  font-size: 17px;
  color: #2c3e50;
  margin: 0;
  font-weight: 600;
  text-align: center;
}

.header-divider {
  width: 50px;
  height: 2.5px;
  background: #409eff;
  margin: 0 auto;
}

/* 信息区 */
.doctor-info-wrap {
  display: flex;
  flex-direction: column;
  gap: 18px; /* 调整信息项间距 */
}

.info-item {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  font-size: 15px;
  line-height: 1.7;
}

.info-label {
  width: 85px;
  color: #7f8c8d;
  font-weight: 500;
}

.info-content {
  color: #34495e;
  flex: 1;
}

/* 擅长方向优化 */
.info-expertise {
  align-items: flex-start;
}

/* 卡片底部装饰 */
.card-footer-line {
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #409eff, transparent);
  margin-top: auto;
}

</style>
