<template>
  <div id="doc-regis">
    <h1 class="title">新医生注册</h1>
    <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
      <el-form-item label="新医生账号:" class="item" style="white-space: nowrap;">
        <el-input v-model="formLabelAlign.account"></el-input>
      </el-form-item>
      <el-form-item label="新医生密码:" class="item" style="white-space: nowrap;">
        <el-input v-model="formLabelAlign.password"></el-input>
      </el-form-item>
      <el-form-item label="医生姓名:" class="item">
        <el-input v-model="formLabelAlign.name"></el-input>
      </el-form-item>
      <el-form-item label="性别:" class="item">
        <el-input v-model="formLabelAlign.gender"></el-input>
      </el-form-item>
      <el-form-item label="年龄:" class="item">
        <el-input v-model.number="formLabelAlign.age"></el-input>
      </el-form-item>
      <el-form-item label="科室:" class="item">
        <el-input v-model="formLabelAlign.office"></el-input>
      </el-form-item>
      <el-form-item label="职位:" class="item">
        <el-input v-model="formLabelAlign.position"></el-input>
      </el-form-item>
      <el-form-item label="擅长方向:" class="item">
        <el-input v-model="formLabelAlign.study"></el-input>
      </el-form-item>
      <el-row>
        <el-button type="primary" @click="submitGegis" class="btn">注册</el-button>
      </el-row>

    </el-form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
    name:'DocRegis',
    data(){
        return {
          labelPosition: 'right',
          formLabelAlign: {
            account:'',
            password:'',
            name:'',
            gender:'',
            office:'',
            position:'',
            study:'',
            age:''
          }
        }
    },
    methods:{
      async sendRegis(){
        try {
          const response = await axios.post('http://192.168.130.45:3333/doctor/register',this.formLabelAlign)

          if(response.data.code === 200){
            alert('注册成功')
            console.log('医生注册成功！')
            return
          }else{
            alert('注册失败')
            console.log(response.data.msg)
          }
        }catch(err){
          console.error(err)
        }
      },
      submitGegis(){
        this.sendRegis()
      }
    }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}
#doc-regis {
  width: 500px;
  padding: 50px;
  border-radius: 8px;
  border: 1px solid rgb(204, 202, 202, 0.5);
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 20px auto;
}
.btn {
  width: 300px;
  height: 40px;
  margin: 20px;
}
.item {
  margin: 10px;
}
.title {
  margin-bottom: 20px;
  color: #096dd9;
  opacity: 0.9;
}
</style>