<template>
  <div id="doc-list">
    <el-table
      :data="tableData"
      border
      style="width: 100%"
    >
      <el-table-column
        prop="name"
        label="医生姓名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="gender"
        label="性别"
        width="180">
      </el-table-column>
      <el-table-column
        prop="office"
        label="科室">
      </el-table-column>
      <el-table-column
        prop="position"
        label="职位">
      </el-table-column>
      <el-table-column
        prop="study"
        label="擅长方向">
      </el-table-column>
      <!-- 插槽，拿到医生的信息里的id，看下是不是id,核查 -->
      <el-table-column>
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="ToRegister(scope.row)"
          >
            去挂号
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios';

export default {
    name:'DoctorList',
    data(){
      return {
        // tableData: [
        //   {
