        //     gender:'男',
        //     office:'内科',
        //     position:'教授',
        //     study:'消化不良、肚子疼',
        //   },
        // ],
        tableData:[]
      }
    },
    created(){
      this.getDoctorData()
    },  
    methods:{
      async getDoctorData(){
        try{
          const response = await axios.get('http://192.168.130.45:3333/patient/doctor-list',{
            params:{
              userId: this.$user.userId
            }
          })

          // const token = localStorage.getItem('token')
          // const response = await axios.get('http://192.168.130.240:3334/patient/register/doctor',{
          //   params:{
          //     userId: this.$user.userId,
          //   },
          //   headers:{
          //     Authorization: token
          //   }
          // })
          //核实码
          if(response.data.code === 200){
            //list有id但是不显示
            // this.tableData = response.data.data
            // 映射，去除多余信息
            const backEndList = response.data.data
            const doctorInfoList = backEndList.map((item) => {
              return {
                doctorId: item.doctorId,
                name: item.name,
                gender: item.gender,
                office: item.office,
                position: item.position,
                study: item.study
              }
            })
            console.log(doctorInfoList)
            // 赋值
            this.tableData = doctorInfoList
          }
        }catch(err){
          console.error('请求失败',err)
        }
      },
      ToRegister(doctorInfo){
        this.$router.push({
          path:'/patient/register',
          query:{
            doctorId: doctorInfo.doctorId,
            name: doctorInfo.name,
            gender: doctorInfo.gender,
            office: doctorInfo.office,
            position: doctorInfo.position,
            study: doctorInfo.study
          }
        })
      }
    }
}
</script>

