<template>
  <div id="outer">
    <el-table
      :data="tableData"
      stripe
      style="width: 100%"
    >
      <!-- 编号,type='index'自动编号 -->
      <el-table-column
        type="index"
        label="编号"
        width="100"
      >
      </el-table-column>
      <!-- 时段 -->
      <el-table-column
        prop="time"
        label="时段"
        width="150"
      >
      </el-table-column>
      <!-- 号种 -->
      <el-table-column
        prop="type"
        label="号种"
        width="130"
      >
      </el-table-column>
      <!-- 费用 -->
      <el-table-column
        prop="fee"
        label="费用"
        width="120"
      >
      </el-table-column>
      <!-- 去挂号 -->
      <el-table-column
        label="操作"
        width="150" 
      >
        <template slot-scope="scope">
          <el-row>
            <el-button 
              type="primary" 
              size="mini" 
              @click="toRegis(scope.row)" 
            >
              挂号
            </el-button>
          </el-row>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name:'ReForm',
  props: {
    doctorInfo: {
      type: Object, 
      required: true, 
    }
  },
  data() {
    return {
      // tableData:[],
      tableData: [
        // {
        //   time: '09:00-10:00',
        //   type: '专家号',
        //   fee: 30
        // },
        // {
        //   time: '10:00-10:30',
        //   type: '普通号',
        //   fee: 15
        // },
        // {
        //   time: '10:30-11:00',
        //   type: '普通号',
        //   fee: 15
        // }
      ],
      patientName: '',
      // post的信息
      singleRegisData: ''
    }
  },
  methods:{
    async getRegisInfo(){
      try {
        const response = await axios.get('http://192.168.130.45:3333/patient/selectDoctorRegistration',{
          params:{
            doctorId: this.$route.query.doctorId
          }
        })
        console.log(this.$route.query.doctorId)
        // data还是list(命名)
        if(response.data.code === 200){
          const backEndList = response.data.data 
          console.log('患者看某医生挂号时间成功！')
          console.log(backEndList)
          const wantList = backEndList.map((item) => {
          return {
            time: item.time,
            type: item.type,
            fee: item.fee
            }
          })
          this.tableData = wantList
        }
      }catch(err){
        console.error('请求失败',err)
      }
    },
    async sendRegisInfo(rowData){
      try {
        const sendDoctorData = {
          doctorId: this.doctorInfo.doctorId,
          doctorName: this.doctorInfo.name,
          name: this.patientName,
          userId: this.$user.userId,
          time: rowData.time,
          type: rowData.type,
          // 没有写，补充！！！！！！！
          fee: rowData.fee
        }
        console.log(sendDoctorData)
        const response = await axios.post('http://192.168.130.45:3333/patient/post',sendDoctorData)
        if(response.data.code === 200){
          alert('挂号成功！')
          console.log('挂号成功')
        }
        // console.log(sendDoctorData)
      }catch(err){
        console.error('请求失败',err)
      }
    },
    toRegis(rowData){
      this.sendRegisInfo(rowData)
    },
    getPatientName(patientName){
      this.patientName = patientName
    }
  },
  created(){
    this.$bus.$on('getName',this.getPatientName)
    this.getRegisInfo()
    console.log(this.$route.query.doctorId)
  },
  beforeDestroy(){
    this.$bus.$off('getName')
  }
}
</script>

