import VueRouter from "vue-router";

//登录页
import TheLogin from "../pages/TheLogin.vue";
//患者
import PatientPage from "../pages/PatientPage.vue";
import DoctorList from "../components/DoctorList.vue";
import CardForm from "@/pages/CardForm.vue";
import PaRecord from "@/components/PaRecord.vue";
//医生
import DoctorPage from "@/pages/DoctorPage.vue";
import DocInfo from "@/components/DocInfo.vue";
import DocSchedule from "@/components/DocSchedule.vue";
import RegisManage from "@/components/RegisManage.vue";
import DocRegis from "@/components/DocRegis.vue";

//创建并暴露一个路由器
const router = new VueRouter({
    routes:[
        {
            path:'/',
            redirect:'/login'
        },
        {
            path:'/login',
            component:TheLogin
        },
        {
            path:'/patient',
            component:PatientPage,
            redirect:'/patient/doctor-list',
            meta:{
                isAuth:true,
                requireRole:0,
            },
            children:[
                {
                    path:'doctor-list',
                    component:DoctorList,
                },
                {
                    path:'register',
                    component:CardForm,
                },
                {
                    path:'record',
                    component:PaRecord
                }
            ]
        },
        {
            path:'/doctor',
            component:DoctorPage,
            redirect:'/doctor/doc-info',
            meta:{
                isAuth:true,
                requireRole:1,
            },
            children:[
                {
                    path:'doc-info',
                    component:DocInfo
                },
                {
                    path:'doc-schedule',
                    component:DocSchedule
                },
                {
                    path:'regis-manage',
                    component:RegisManage
                },
                {
                    path:'regis',
                    component:DocRegis
                }
            ]
        }
    ]
})

// //全局前置守卫
router.beforeEach((to, from, next) => {
  const isLogin = localStorage.getItem('token')
  // 修正：从userAuth中获取角色信息
  const userAuth = JSON.parse(localStorage.getItem('userAuth') || '{}')
  const roleId = userAuth.role
 
  const requireRole = to.meta.requireRole
  const requireAuth = to.meta.isAuth
 
  if (to.path === '/' || to.path === '/login') return next();
  
  if (requireAuth) {
    if (!isLogin) return next('/')
    // 修正：直接比较角色码而非用户ID
    if (Number(requireRole) !== Number(roleId)) {
      alert('无权限访问该页面')
      return next(false) // 阻止继续跳转
    }
    next()
  } else {
    next()
  }
})

export default router