import Vue from 'vue'
import App from './App.vue'
//引入VueRouter
import VueRouter from 'vue-router'
//引入router文件
import router from './router'
//引入store
import store from './store'
//按需引入element-ui
import { MessageBox,Message,Menu,MenuItem,Table,TableColumn,Button,Form,FormItem,Input,Select,Option,Tag,Dropdown,DropdownMenu,DropdownItem,Row } from 'element-ui'

//注册element-ui
//菜单导航区
Vue.use(Menu)
Vue.use(MenuItem)
//医生列表
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Button)
Vue.use(Row)
//患者挂号
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)
Vue.use(Select)
Vue.use(Option)
//患者挂号记录
Vue.use(Tag)
//医生挂号管理
Vue.use(Dropdown)
Vue.use(DropdownMenu)
Vue.use(DropdownItem)


Vue.config.productionTip = false
//注册VueRouter
Vue.use(VueRouter)

//刷新后从本地存储恢复userId
Vue.prototype.$user = { userId:'' }
const savedUser = localStorage.getItem('userAuth')
if(savedUser){
  Vue.prototype.$user.userId = JSON.parse(savedUser).userId
}

// 挂载 Message 到 Vue 原型  
Vue.prototype.$message = Message;  
// Vue.prototype.$loading = Loading;  
  
// 关键步骤：挂载 MessageBox 的 confirm 方法到 Vue 原型  
Vue.prototype.$confirm = MessageBox.confirm; 

new Vue({
  render: h => h(App),
  router,
  store,
  //全局事件总线
  beforeCreate(){
    Vue.prototype.$bus = this
  }
}).$mount('#app')
