<template>
  <div class="order-record-page">
    <h2>我的挂号记录</h2>
    <!-- 订单表格 -->
    <el-table 
      :data="orderList" 
      border 
      stripe 
      style="width:951px ; margin-top: 20px;"
    >
      <el-table-column 
        prop="id" 
        label="订单号" 
        align="center"
        width="180"
      ></el-table-column>
      <el-table-column 
        prop="doctorName" 
        label="医生姓名" 
        align="center"
        width="120"
      ></el-table-column>
      <el-table-column 
        prop="registerTime" 
        label="挂号时段" 
        align="center"
        width="220"
      ></el-table-column>
      <el-table-column 
        prop="type" 
        label="号种" 
        align="center"
        width="210"
      ></el-table-column>
      <el-table-column 
        prop="status" 
        label="订单状态" 
        align="center"
        width="220"
      >
        <template slot-scope="scope">
          <el-tag 
            :type="getStatusTagType(scope.row.status)"
          >
            {{ getStatusText(scope.row.status) }}
          </el-tag>
        </template>
      </el-table-column>
    </el-table>

    <!-- 加载中提示 -->
    <!-- <el-loading 
      v-if="isLoading" 
      text="正在加载订单数据..." 
      fullscreen
    ></el-loading> -->

    <!-- 空数据提示 -->
    <!-- <div v-if="!isLoading && orderList.length === 0" class="empty-tip">
      暂无挂号记录
    </div> -->
  </div>
</template>

<script>
import axios from 'axios'

export default {
    name:'PaRecord',
    data(){
        return {
          orderList:[
            // {
            //   orderNo: 'HYSJSJ12234',
            //   doctorName: '朱医生',
            //   registerTime: '9:00-10:00',
            //   type:'专家号',
            //   status: 1
            // },
            // {
            //   orderNo: 'HYSJSJ26146',
            //   doctorName: '刘医生',
            //   registerTime: '10:00-11:00',
            //   type:'专家号',
            //   status: 2
            // },
            // {
            //   orderNo: 'HYSJSJ21426',
            //   doctorName: '张医生',
            //   registerTime: '11:00-11:30',
            //   type:'普通号',
            //   status: 0
            // }
          ],
          isLoading:false,
          // 映射
          statusMap:{
            0: '待就诊',
            1: '已就诊',
            2: '失约'
          },
          // 标签颜色,蓝，绿，红
          statusTagTypeMap:{
            0: 'warning',
            1: 'success',
            2: 'danger'
          }
        }
    },
    created(){
      this.getOrderList()
    },
    methods:{
      async getOrderList(){
        this.isLoading = true
        try {          
          const response = await axios.get('http://192.168.130.45:3333/patient/record',{
            params: { 
              userId: this.$user.userId  
            } 
          })
          if(response.data.code === 200){
            const regisInfoList = response.data.data
            const okInfoList = regisInfoList.map((item) => {
              return {
                id: item.id,
                doctorName: item.doctorName,
                registerTime: item.time,
                type: item.type,
                status: item.status
              }
            })
            this.orderList = okInfoList
          }
        }catch(error){
          this.$message.error('请求出错',error)
        }finally{
          this.isLoading = false
        }
      },
      getStatusText(status){
        // 映射
        return this.statusMap[status] || '未知状态'
      },
      getStatusTagType(status){
        return this.statusTagTypeMap[status] || 'default'
      }
    }
}
</script>

<style scoped>
.order-record-page {
  justify-content: center;
  align-items: center;
  padding-left: 100px;
}
</style>